html.contrast-mode ::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 0.6rem rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 0 0.6rem rgba(0, 0, 0, 0.2);
  background: yellow; }

html.contrast-mode ::selection {
  background-color: yellow;
  color: black; }

html.contrast-mode body {
  background-color: black;
  color: yellow; }

html.wcagfocus a:focus,
html.wcagfocus input[type="text"]:focus,
html.wcagfocus input[type="search"]:focus,
html.wcagfocus input[type="submit"]:focus,
html.wcagfocus textarea:focus,
html.wcagfocus .pe-panels.tabs .pn-list li a:focus,
html.wcagfocus *:focus {
  outline: 0.2rem dashed red; }
